import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'https://curvotech.onrender.com/api';

/**
 * Ly ni dung gi hng
 * @returns {Promise} Thng tin gi hng
 */
export const getCart = async () => {
    try {
        const response = await axios.get(`${API_URL}/cart/cart`, { withCredentials: true });
        return response.data;
    } catch (error) {
        console.error('Li khi ly gi hng:', error);
        throw error;
    }
};

/**
 * Thm sn phm vo gi hng
 * @param {string} productId - ID ca sn phm
 * @returns {Promise} Kt qu thm vo gi hng
 */
export const addToCart = async (productId) => {
    try {
        const response = await axios.post(`${API_URL}/cart/cart/add/${productId}`, {}, { withCredentials: true });
        return response.data;
    } catch (error) {
        console.error('Li khi thm vo gi hng:', error);
        throw error;
    }
};

/**
 * Tng s lng sn phm trong gi hng
 * @param {string} productId - ID ca sn phm
 * @returns {Promise} Kt qu cp nht gi hng
 */
export const incrementCartItem = async (productId) => {
    try {
        const response = await axios.post(`${API_URL}/cart/cart/increment/${productId}`, {}, { withCredentials: true });
        return response.data;
    } catch (error) {
        console.error('Li khi tng s lng:', error);
        throw error;
    }
};

/**
 * Gim s lng sn phm trong gi hng
 * @param {string} productId - ID ca sn phm
 * @returns {Promise} Kt qu cp nht gi hng
 */
export const decrementCartItem = async (productId) => {
    try {
        const response = await axios.post(`${API_URL}/cart/cart/decrement/${productId}`, {}, { withCredentials: true });
        return response.data;
    } catch (error) {
        console.error('Li khi gim s lng:', error);
        throw error;
    }
};

/**
 * Xa sn phm khi gi hng
 * @param {string} productId - ID ca sn phm
 * @returns {Promise} Kt qu cp nht gi hng
 */
export const removeFromCart = async (productId) => {
    try {
        const response = await axios.post(`${API_URL}/cart/cart/remove/${productId}`, {}, { withCredentials: true });
        return response.data;
    } catch (error) {
        console.error('Li khi xa khi gi hng:', error);
        throw error;
    }
};

/**
 * Xa ton b gi hng
 * @returns {Promise} Kt qu xa gi hng
 */
export const clearCart = async () => {
    try {
        const response = await axios.post(`${API_URL}/cart/cart/clear`, {}, { withCredentials: true });
        return response.data;
    } catch (error) {
        console.error('Li khi xa ton b gi hng:', error);
        throw error;
    }
};

/**
 * Thm sn phm vo gi hng vi thng bo
 * @param {string} productId - ID ca sn phm 
 * @param {Function} showNotification - Hm hin th thng bo (ty chn)
 */
const handleAddToCart = async (productId, showNotification) => {
    try {
        await addToCart(productId);
        
        // S dng thng bo nu c cung cp, ngc li dng alert
        if (typeof showNotification === 'function') {
            showNotification(' thm sn phm vo gi hng!', 'success');
        } else {
            alert(`  thm sn phm vo gi hng!`);
        }
    } catch (err) {
        console.error(' Li thm vo gi hng:', err);
        if (typeof showNotification === 'function') {
            showNotification('C li xy ra khi thm sn phm vo gi hng!', 'error');
        } else {
            alert(' C li xy ra khi thm sn phm vo gi hng!');
        }
    }
};

export default handleAddToCart;
