import axios from 'axios';

// S dng bin mi trng cho API URL
const API_URL = process.env.REACT_APP_API_URL || 'https://curvotech.onrender.com/api/users';

// To axios instance vi cu hnh chung
const axiosInstance = axios.create({
  baseURL: API_URL,
  withCredentials: true,
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json',
  }
});

// X l li chung
const handleError = (error) => {
  const errorMessage = error.response?.data?.message || ' xy ra li';
  throw new Error(errorMessage);
};

// Thm interceptor  x l request
axiosInstance.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// Thm interceptor  x l response
axiosInstance.interceptors.response.use(
  (response) => response,
  (error) => {
    // Nu c li 401 Unauthorized
    if (error.response?.status === 401) {
      const errorMessage = error.response?.data?.message || '';
      
      // Kim tra nu l li mt khu hin ti khng ng, khng xa token
      if (errorMessage.includes('hin ti') || 
          errorMessage.includes('current password') || 
          errorMessage.includes('khng ng')) {
        console.log('Current password error, not removing token');
      } else {
        // Cc li xc thc khc th xa token
        localStorage.removeItem('token');
        console.log('Token removed due to 401 error: ', errorMessage);
      }
    }
    return Promise.reject(error);
  }
);

/**
 * ng k ti khon mi
 * @param {string} username - Tn ng nhp
 * @param {string} password - Mt khu
 * @param {string} confirmPassword - Xc nhn mt khu
 * @returns {Promise} Thng tin ngi dng sau khi ng k
 */
export const register = async (username, password, confirmPassword) => {
  try {
    const response = await axiosInstance.post('/register', {
      username,
      password,
      confirmPassword,
    });
    localStorage.setItem('token', response.data.token);
    return response.data;
  } catch (error) {
    handleError(error);
  }
};

/**
 * ng nhp
 * @param {string} username - Tn ng nhp
 * @param {string} password - Mt khu
 * @returns {Promise} Thng tin ngi dng sau khi ng nhp
 */
export const login = async (username, password) => {
  try {
    const response = await axiosInstance.post('/login', {
      username,
      password,
    });
    localStorage.setItem('token', response.data.token);
    return response.data;
  } catch (error) {
    handleError(error);
  }
};

/**
 * ng xut
 * @returns {Promise} Kt qu ng xut
 */
export const logout = async () => {
  try {
    const response = await axiosInstance.post('/logout');
    // Xa token
    localStorage.removeItem('token');
    // Xa headers authorization
    delete axiosInstance.defaults.headers.common['Authorization'];
    return response.data;
  } catch (error) {
    // m bo xa token ngay c khi c li
    localStorage.removeItem('token');
    delete axiosInstance.defaults.headers.common['Authorization'];
    handleError(error);
  }
};

/**
 * Ly thng tin ngi dng
 * @returns {Promise} Thng tin ngi dng
 */
export const getProfile = async () => {
  try {
    // Kim tra token trc khi gi API
    const token = localStorage.getItem('token');
    if (!token) {
      throw new Error('No token found');
    }
    
    const response = await axiosInstance.get('/profile');
    return response.data;
  } catch (error) {
    // Li  c x l trong interceptor
    return Promise.reject(error);
  }
};

/**
 * i mt khu
 * @param {string} currentPassword - Mt khu hin ti
 * @param {string} newPassword - Mt khu mi
 * @returns {Promise} Kt qu i mt khu
 */
export const changePassword = async (currentPassword, newPassword) => {
  try {
    // Kim tra token trc khi gi API
    const token = localStorage.getItem('token');
    if (!token) {
      throw new Error('No token found');
    }
    
    const response = await axiosInstance.put('/change-password', {
      currentPassword,
      newPassword
    });
    return response.data;
  } catch (error) {
    //  c x l trong interceptor, ch cn chuyn tip li
    return Promise.reject(error);
  }
};