import React, { useEffect, useState } from 'react';
import { useParams, useSearchParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { verifyPayment } from '../services/paymentService';
import { clearCart } from '../services/cartService';

const PaymentResult = () => {
  const [status, setStatus] = useState('loading'); // loading, success, error
  const [message, setMessage] = useState('');
  const [orderData, setOrderData] = useState(null);
  const [searchParams] = useSearchParams();
  const { type } = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    const verifyPaymentStatus = async () => {
      try {
        // Xa gi hng ngay lp tc khi hin th trang kt qu thanh ton
        await clearCart();
        console.log(' xa gi hng  client');
      
        // Nu l t MoMo quay v
        if (searchParams.get('orderId')) {
          const orderId = searchParams.get('orderId');
          const resultCode = searchParams.get('resultCode');
          
          console.log(`MoMo payment return with orderId: ${orderId}, resultCode: ${resultCode}`);
          
          if (resultCode === '0') {
            try {
              // Thnh cng t Momo, cp nht trng thi
              console.log('Payment successful according to MoMo, verifying with backend');
              const verifyResult = await verifyPayment(orderId, 'momo');
              console.log('Verification result:', verifyResult);
              
              if (verifyResult.success) {
                setStatus('success');
                setMessage('Thanh ton thnh cng! n hng ca bn ang c x l.');
                setOrderData(verifyResult);
              } else {
                // Him khi xy ra v backend  t ng cp nht trng thi
                setStatus('error');
                setMessage('Khng th xc minh trng thi thanh ton. Vui lng lin h h tr.');
              }
            } catch (error) {
              console.error('Verification error:', error);
              
              // Nu MoMo bo thnh cng, hin th thnh cng cho ngi dng
              // d c li khi xc minh (v tin  b tr)
              setStatus('success');
              setMessage('Thanh ton  c MoMo xc nhn. Vui lng lin h h tr nu n hng khng c cp nht trong vi pht.');
            }
          } else {
            // Tht bi t MoMo
            setStatus('error');
            setMessage(`Thanh ton tht bi: ${searchParams.get('message') || 'Li khng xc nh'}`);
          }
        } 
        // Nu l chuyn khon ngn hng
        else if (type === 'banking') {
          setStatus('pending');
          setMessage('Vui lng hon tt chuyn khon ngn hng theo thng tin bn di.');

          // Ly thng tin giao dch t server
          const orderId = searchParams.get('id');
          if (orderId) {
            const orderDetails = await axios.get(`https://curvotech.onrender.com/api/payment/verify/${orderId}`);
            setOrderData(orderDetails.data);
          }
        }
        // Trng hp thnh cng chung
        else if (type === 'success') {
          setStatus('success');
          setMessage('n hng  c to thnh cng! Cm n bn  mua sm.');
        }
        // Mc nh nu khng c thng tin
        else {
          setStatus('error');
          setMessage('Khng tm thy thng tin thanh ton. Vui lng kim tra li n hng ca bn.');
        }
      } catch (error) {
        console.error('Li xc minh thanh ton:', error);
        setStatus('error');
        setMessage(' xy ra li khi xc minh thanh ton. Vui lng lin h h tr.');
      }
    };

    verifyPaymentStatus();
  }, [searchParams, type]);

  const getStatusIcon = () => {
    switch (status) {
      case 'success':
        return (
          <div className="w-24 h-24 rounded-full bg-green-100 flex items-center justify-center mx-auto mb-6">
            <svg className="w-16 h-16 text-green-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7"></path>
            </svg>
          </div>
        );
      case 'error':
        return (
          <div className="w-24 h-24 rounded-full bg-red-100 flex items-center justify-center mx-auto mb-6">
            <svg className="w-16 h-16 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path>
            </svg>
          </div>
        );
      case 'pending':
        return (
          <div className="w-24 h-24 rounded-full bg-yellow-100 flex items-center justify-center mx-auto mb-6">
            <svg className="w-16 h-16 text-yellow-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"></path>
            </svg>
          </div>
        );
      default:
        return (
          <div className="w-24 h-24 rounded-full bg-blue-100 flex items-center justify-center mx-auto mb-6">
            <svg className="w-16 h-16 text-blue-500 animate-spin" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
              <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
              <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
          </div>
        );
    }
  };

  const renderBankingDetails = () => {
    if (status !== 'pending' || !orderData?.paymentDetails) return null;

    return (
      <div className="mt-6 border rounded-lg p-6 bg-yellow-50">
        <h3 className="text-lg font-bold mb-4">Thng tin chuyn khon</h3>
        
        <div className="space-y-2">
          <div className="flex justify-between">
            <span className="font-medium">Ngn hng:</span>
            <span>{orderData.paymentDetails.bankId}</span>
          </div>
          
          <div className="flex justify-between">
            <span className="font-medium">S ti khon:</span>
            <span>0123456789</span>
          </div>
          
          <div className="flex justify-between">
            <span className="font-medium">Ch ti khon:</span>
            <span>CONG TY MERN</span>
          </div>
          
          <div className="flex justify-between">
            <span className="font-medium">S tin:</span>
            <span className="text-red-600 font-bold">{orderData.paymentDetails.amount.toLocaleString('vi-VN')} VN</span>
          </div>
          
          <div className="flex justify-between">
            <span className="font-medium">Ni dung chuyn khon:</span>
            <span className="font-bold">{orderData.paymentDetails.transferContent}</span>
          </div>
        </div>
        
        <div className="mt-4 p-3 bg-yellow-100 rounded-lg text-sm">
          <p className="font-medium">Lu :</p>
          <ul className="list-disc pl-5 mt-1">
            <li>Vui lng chuyn khon chnh xc s tin v ni dung chuyn khon</li>
            <li>n hng s c x l trong vng 24h sau khi nhn c thanh ton</li>
            <li>Nu cn h tr, vui lng lin h qua email: support@example.com</li>
          </ul>
        </div>
      </div>
    );
  };

  return (
    <div className="bg-gray-100 min-h-screen py-12">
      <div className="max-w-xl mx-auto bg-white rounded-lg shadow-md p-8">
        {getStatusIcon()}
        
        <h1 className={`text-2xl font-bold text-center mb-4 ${
          status === 'success' ? 'text-green-600' : 
          status === 'error' ? 'text-red-600' : 
          status === 'pending' ? 'text-yellow-600' : 'text-blue-600'
        }`}>
          {status === 'success' ? 'Thanh ton thnh cng!' : 
           status === 'error' ? 'Thanh ton tht bi!' :
           status === 'pending' ? 'Ch thanh ton' : 'ang x l...'}
        </h1>
        
        <p className="text-center text-gray-700 mb-6">{message}</p>
        
        {renderBankingDetails()}
        
        <div className="flex justify-center mt-8 space-x-4">
          <button 
            onClick={() => navigate('/products')}
            className="px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
          >
            Tip tc mua sm
          </button>
          
          {status === 'success' && (
            <button 
              onClick={() => navigate('/user/orders')}
              className="px-6 py-2 bg-gray-200 text-gray-800 rounded-lg hover:bg-gray-300 transition-colors"
            >
              Xem n hng
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default PaymentResult; 