import axios from 'axios';
import authHeader from '../utils/authHeader';

// ng dn API 
const API_URL = 'https://curvotech.onrender.com/api';

/**
 * Log  debug request
 */
const logRequest = (method, url, headers) => {
  console.log(`[${method}] ${url}`);
  console.log('Headers:', JSON.stringify(headers, null, 2));
};

/**
 * Ly danh sch n hng ca ngi dng  ng nhp
 * @param {Object} options - Cc ty chn lc v phn trang
 * @param {string} options.status - Trng thi n hng (optional)
 * @param {number} options.page - Trang hin ti
 * @param {number} options.limit - S n hng mi trang
 * @returns {Promise} Danh sch n hng v thng tin phn trang
 */
export const getUserOrders = async (options = {}) => {
  try {
    const { status, page = 1, limit = 10 } = options;
    
    // Xy dng query string
    const params = new URLSearchParams();
    if (status) params.append('status', status);
    params.append('page', page);
    params.append('limit', limit);
    
    const url = `${API_URL}/orders?${params.toString()}`;
    const headers = authHeader();
    
    // Log  debug
    logRequest('GET', url, headers);
    
    const response = await axios.get(url, { headers });
    
    return {
      success: true,
      orders: response.data.orders || [],
      pagination: response.data.pagination || {
        total: response.data.total || 0,
        page: response.data.page || 1,
        limit: response.data.limit || 10,
        totalPages: response.data.totalPages || 0
      }
    };
  } catch (error) {
    console.error('Li khi ly danh sch n hng:', error.response?.data || error.message);
    let errorMessage = 'Khng th kt ni n my ch';
    
    if (error.response) {
      errorMessage = error.response.data.message || errorMessage;
    }
    
    return {
      success: false,
      message: errorMessage,
      orders: [],
      pagination: {
        total: 0,
        page: 1,
        limit: 10,
        totalPages: 0
      }
    };
  }
};

// Alias for backward compatibility
export const getMyOrders = getUserOrders;

/**
 * Ly chi tit n hng theo ID
 * @param {string} orderId - ID ca n hng
 * @returns {Promise} Chi tit n hng
 */
export const getOrderById = async (orderId) => {
  try {
    const url = `${API_URL}/orders/${orderId}`;
    const headers = authHeader();
    
    // Log  debug
    logRequest('GET', url, headers);
    
    const response = await axios.get(url, { headers });
    
    return {
      success: true,
      order: response.data.order || response.data || {}
    };
  } catch (error) {
    console.error(`Li khi ly chi tit n hng ID ${orderId}:`, error.response?.data || error.message);
    let errorMessage = 'Khng th kt ni n my ch';
    
    if (error.response) {
      errorMessage = error.response.data.message || errorMessage;
    }
    
    return {
      success: false,
      message: errorMessage,
      order: {}
    };
  }
};

/**
 * Hy n hng
 * @param {string} orderId - ID ca n hng
 */
export const cancelOrder = async (orderId) => {
  try {
    const url = `${API_URL}/orders/${orderId}/cancel`;
    const headers = authHeader();
    
    // Log  debug
    logRequest('PATCH', url, headers);
    
    const response = await axios.patch(url, {}, { headers });
    return response.data;
  } catch (error) {
    console.error('Error cancelling order:', error.response?.data || error.message);
    throw error;
  }
};

/**
 * To n hng mi
 * @param {Object} orderData - D liu n hng cn to
 */
export const createOrder = async (orderData) => {
  try {
    const url = `${API_URL}/orders`;
    const headers = authHeader();
    
    // Log  debug
    logRequest('POST', url, headers);
    console.log('Order data:', JSON.stringify(orderData, null, 2));
    
    const response = await axios.post(url, orderData, { headers });
    return response.data;
  } catch (error) {
    console.error('Error creating order:', error.response?.data || error.message);
    throw error;
  }
}; 