import React, { useEffect, useState, useCallback, useMemo } from 'react';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { useNotification } from '../contexts/NotificationContext';
import { toast } from 'react-hot-toast';
import { useSelector } from 'react-redux';
import Spinner from '../components/Spinner';

const Cart = () => {
  console.log('Cart component rendering');

  const [cart, setCart] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [processing, setProcessing] = useState(false);
  const { showNotification } = useNotification();
  const navigate = useNavigate();
  const { user } = useSelector(state => state.user);

  // S dng useMemo  to axiosInstance n nh, khng b to mi mi ln render
  const axiosInstance = useMemo(() => axios.create({
    baseURL: 'https://curvotech.onrender.com',
    withCredentials: true,
    headers: {
      'Content-Type': 'application/json'
    }
  }), []);

  // S dng useCallback  nh ngha fetchCart vi dependency n nh
  const fetchCart = useCallback(async () => {
    setLoading(true);
    setError(null);
    
    try {
      console.log('Fetching cart data...');
      const res = await axiosInstance.get('/api/cart/cart');
      console.log('Cart response:', res.data);
      
      if (!res.data.cart || res.data.cart.length === 0) {
        console.log('Cart is empty, setting empty cart state');
        setCart([]);
        return;
      }
      
      // Ly thng tin chi tit cho tng sn phm trong gi hng
      const cartWithDetails = await Promise.all(
        res.data.cart.map(async (item) => {
          try {
            const productRes = await axiosInstance.get(`/api/products/${item.productId}`);
            const product = productRes.data.product || {};
            return {
              _id: item.productId,
              quantity: item.quantity,
              name: product.name || 'Sn phm khng xc nh',
              price: product.price || 0,
              image: product.image || null,
            };
          } catch (err) {
            console.error(`Error fetching details for product ${item.productId}:`, err);
            return {
              _id: item.productId,
              quantity: item.quantity,
              name: 'Sn phm khng tn ti',
              price: 0,
              image: null,
            };
          }
        })
      );
      
      setCart(cartWithDetails);
    } catch (err) {
      console.error('Li khi ti gi hng:', err);
      setError(`Khng th ti gi hng: ${err.message || err.toString()}`);
    } finally {
      setLoading(false);
    }
  }, [axiosInstance]);

  useEffect(() => {
    fetchCart();
  }, [fetchCart]); // Ch gi fetchCart khi component mount hoc khi axiosInstance thay i (hin  n nh)

  // Debug log cho cc state
  useEffect(() => {
    console.log('Current component state:');
    console.log('Loading:', loading);
    console.log('Cart:', cart);
    console.log('Error:', error);
  }, [loading, cart, error]);

  const handleAction = async (action, id, name) => {
    setProcessing(true);
    
    let endpoint, message;
    
    if (action === 'increment') {
      endpoint = `/api/cart/cart/increment/${id}`;
      message = ` tng s lng ${name}`;
    } else if (action === 'decrement') {
      endpoint = `/api/cart/cart/decrement/${id}`;
      message = ` gim s lng ${name}`;
    } else if (action === 'remove') {
      endpoint = `/api/cart/cart/remove/${id}`;
      message = ` xa ${name} khi gi hng`;
    }
    
    try {
      await axiosInstance.post(endpoint);
      toast.success(message);
      
      // Ly d liu gi hng cp nht
      const cartRes = await axiosInstance.get('/api/cart/cart');
      
      if (!cartRes.data.cart || cartRes.data.cart.length === 0) {
        setCart([]);
        setProcessing(false);
        return;
      }
      
      const cartWithDetails = await Promise.all(
        cartRes.data.cart.map(async (item) => {
          try {
            const productRes = await axiosInstance.get(`/api/products/${item.productId}`);
            const product = productRes.data.product || {};
            return {
              _id: item.productId,
              quantity: item.quantity,
              name: product.name || 'Sn phm khng xc nh',
              price: product.price || 0,
              image: product.image || null,
            };
          } catch (err) {
            console.error(`Error fetching details for product ${item.productId}:`, err);
            return {
              _id: item.productId,
              quantity: item.quantity,
              name: 'Sn phm khng tn ti',
              price: 0,
              image: null
            };
          }
        })
      );
      setCart(cartWithDetails);
    } catch (error) {
      console.error(`Li khi ${action === 'remove' ? 'xa' : 'cp nht'} sn phm:`, error);
      toast.error(`Khng th ${action === 'remove' ? 'xa' : 'cp nht'} sn phm. Vui lng th li.`);
    } finally {
      setProcessing(false);
    }
  };

  const increment = (id, name) => handleAction('increment', id, name);
  const decrement = (id, name) => handleAction('decrement', id, name);
  const remove = (id, name) => handleAction('remove', id, name);

  const handleCheckout = () => {
    if (!user) {
      toast.error('Vui lng ng nhp  tip tc thanh ton', { 
        duration: 4000,
        position: 'top-center' 
      });
      navigate('/login', { state: { from: { pathname: '/checkout' } } });
      return;
    }
    
    if (cart.length === 0) {
      showNotification('Gi hng trng, khng th thanh ton', 'error');
    } else {
      navigate('/checkout');
    }
  };

  const total = cart.reduce((sum, p) => sum + (p.price ? p.price * p.quantity : 0), 0);

  if (loading) {
    console.log('Rendering loading state');
    return (
      <div className="container mx-auto p-4 text-center">
        <h1 className="text-2xl font-bold mb-4"> Gi hng ca bn</h1>
        <div className="py-10">
          <div className="animate-spin rounded-full h-10 w-10 border-b-2 border-primary mx-auto"></div>
          <p className="mt-2">ang ti gi hng...</p>
        </div>
      </div>
    );
  }

  if (error) {
    console.log('Rendering error state');
    return (
      <div className="container mx-auto p-4 text-center">
        <h1 className="text-2xl font-bold mb-4"> Gi hng ca bn</h1>
        <div className="py-10 text-red-500">
          <p>{error}</p>
          <p className="text-xs mt-2">Chi tit li: {error && error.toString()}</p>
          <button 
            onClick={fetchCart}
            className="mt-4 bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
          >
            Th li
          </button>
        </div>
      </div>
    );
  }

  console.log('Rendering cart UI, cart length:', cart.length);

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold text-center mb-4"> Gi hng ca bn</h1>
      {cart.length === 0 ? (
        <div className="text-center py-10 bg-white p-4 rounded-lg shadow-md">
          <p className="mb-4">Gi hng trng!</p>
          <Link to="/products" className="bg-blue-500 text-white px-6 py-2 rounded-lg hover:bg-blue-600">
            Mua sm ngay
          </Link>
        </div>
      ) : (
        <div className="space-y-4">
          {cart.map((product) => (
            <div key={product._id} className="flex flex-col md:flex-row items-center bg-white p-4 rounded-lg shadow-md hover:shadow-lg transition-shadow">
              {product.image ? (
                <img 
                  src={product.image} 
                  alt={product.name} 
                  className="w-24 h-24 object-cover rounded-lg"
                  onError={(e) => {
                    e.target.onerror = null;
                    e.target.src = '';
                  }}
                />
              ) : (
                <div className="w-24 h-24 bg-gray-200 flex items-center justify-center rounded-lg">
                  <span className="text-gray-500 text-sm">No image</span>
                </div>
              )}
              <div className="ml-4 flex-grow mt-4 md:mt-0">
                <h2 className="text-lg font-semibold">{product.name}</h2>
                <p className="text-gray-700">
                  Gi: <span className="font-medium text-red-600">{product.price ? product.price.toLocaleString() : 0} VND</span>
                </p>
                <p className="text-gray-700">
                  Tng: <span className="font-medium text-red-600">{product.price ? (product.price * product.quantity).toLocaleString() : 0} VND</span>
                </p>
                <div className="flex items-center mt-2">
                  <span className="mr-2">S lng:</span>
                  <div className="flex items-center border border-gray-300 rounded">
                    <button 
                      onClick={() => decrement(product._id, product.name)} 
                      disabled={processing}
                      className="px-3 py-1 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-l"
                    >
                      -
                    </button>
                    <span className="px-3 py-1 border-l border-r border-gray-300">{product.quantity}</span>
                    <button 
                      onClick={() => increment(product._id, product.name)}
                      disabled={processing}
                      className="px-3 py-1 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-r"
                    >
                      +
                    </button>
                  </div>
                </div>
              </div>
              <button 
                onClick={() => remove(product._id, product.name)}
                disabled={processing}
                className="mt-4 md:mt-0 bg-red-500 text-white px-3 py-2 rounded hover:bg-red-600 transition-colors flex items-center"
              >
                <i className="fas fa-trash-alt mr-2"></i> Xa
              </button>
            </div>
          ))}

          <div className="bg-white p-4 rounded-lg shadow-md">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-xl font-bold">Thng tin n hng</h3>
              <span className="text-gray-600">{cart.length} sn phm</span>
            </div>
            <div className="border-t border-gray-200 pt-4">
              <div className="flex justify-between mb-2">
                <span>Tm tnh:</span>
                <span>{total.toLocaleString()} VND</span>
              </div>
              <div className="flex justify-between font-bold text-lg border-t border-gray-200 pt-2 mt-2">
                <span>Tng cng:</span>
                <span className="text-red-600">{total.toLocaleString()} VND</span>
              </div>
              <button 
                onClick={handleCheckout}
                disabled={processing || cart.length === 0}
                className={`w-full mt-4 py-3 rounded-lg text-white font-medium text-center 
                  ${processing || cart.length === 0 ? 'bg-gray-400 cursor-not-allowed' : 'bg-green-500 hover:bg-green-600'}`}
              >
                {processing ? 'ang x l...' : 'Tin hnh thanh ton'}
              </button>
            </div>
          </div>
        </div>
      )}
      <Link to="/products" className="block text-center mt-6 text-blue-500 hover:underline">
         Tip tc mua sm
      </Link>
    </div>
  );
};

export default Cart;
