import React, { useEffect, useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useNotification } from '../contexts/NotificationContext';
import { getProfile, changePassword } from '../services/userService';

const Profile = () => {
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [passwordData, setPasswordData] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [passwordFormErrors, setPasswordFormErrors] = useState({});
  const [touchedFields, setTouchedFields] = useState({
    currentPassword: false,
    newPassword: false,
    confirmPassword: false
  });
  const navigate = useNavigate();
  const { showNotification } = useNotification();

  useEffect(() => {
    let isMounted = true;
    console.log('Profile useEffect triggered once');

    const fetchProfile = async () => {
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          console.log('No token found, redirecting to login');
          navigate('/login');
          return;
        }

        console.log('Token found, fetching profile...');
        const response = await getProfile();
        
        if (isMounted) {
          setUser(response.user);
          setIsLoading(false);
        }
      } catch (error) {
        console.error('Error fetching profile:', error);
        if (error.response && error.response.status === 401) {
          // Token expired or invalid
          localStorage.removeItem('token');
          if (isMounted) {
            navigate('/login');
          }
        } else if (isMounted) {
          setError('C li xy ra khi ti thng tin ngi dng');
          setIsLoading(false);
        }
      }
    };

    fetchProfile();

    return () => {
      isMounted = false;
    };
  }, [navigate]); // Added navigate to the dependency array

  // Validate mt khu hin ti
  const validateCurrentPassword = (value) => {
    if (!value) {
      return 'Vui lng nhp mt khu hin ti';
    }
    return '';
  };

  // Validate mt khu mi
  const validateNewPassword = (value) => {
    if (!value) {
      return 'Vui lng nhp mt khu mi';
    }
    if (value.length < 6) {
      return 'Mt khu mi phi c t nht 6 k t';
    }
    if (value === passwordData.currentPassword) {
      return 'Mt khu mi khng c trng vi mt khu hin ti';
    }
    return '';
  };

  // Validate xc nhn mt khu
  const validateConfirmPassword = (value) => {
    if (!value) {
      return 'Vui lng xc nhn mt khu mi';
    }
    if (value !== passwordData.newPassword) {
      return 'Mt khu xc nhn khng khp vi mt khu mi';
    }
    return '';
  };

  // Validate real-time khi ngi dng nhp xong tng trng
  const handleBlur = (e) => {
    const { name, value } = e.target;
    setTouchedFields(prev => ({
      ...prev,
      [name]: true
    }));

    let errorMessage = '';
    switch (name) {
      case 'currentPassword':
        errorMessage = validateCurrentPassword(value);
        break;
      case 'newPassword':
        errorMessage = validateNewPassword(value);
        // Nu mt khu mi thay i, cng cn kim tra li trng confirm
        if (touchedFields.confirmPassword && passwordData.confirmPassword) {
          const confirmError = validateConfirmPassword(passwordData.confirmPassword);
          setPasswordFormErrors(prev => ({
            ...prev,
            confirmPassword: confirmError
          }));
        }
        break;
      case 'confirmPassword':
        errorMessage = validateConfirmPassword(value);
        break;
      default:
        break;
    }

    setPasswordFormErrors(prev => ({
      ...prev,
      [name]: errorMessage
    }));
  };

  const handlePasswordChange = (e) => {
    const { name, value } = e.target;
    setPasswordData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Real-time validation khi ngi dng  tng tng tc vi trng ny
    if (touchedFields[name]) {
      let errorMessage = '';
      switch (name) {
        case 'currentPassword':
          errorMessage = validateCurrentPassword(value);
          break;
        case 'newPassword':
          errorMessage = validateNewPassword(value);
          break;
        case 'confirmPassword':
          errorMessage = validateConfirmPassword(value);
          break;
        default:
          break;
      }

      setPasswordFormErrors(prev => ({
        ...prev,
        [name]: errorMessage
      }));
    }
  };

  const validatePasswordForm = () => {
    // nh du tt c cc trng  c tng tc
    setTouchedFields({
      currentPassword: true,
      newPassword: true,
      confirmPassword: true
    });

    const currentPasswordError = validateCurrentPassword(passwordData.currentPassword);
    const newPasswordError = validateNewPassword(passwordData.newPassword);
    const confirmPasswordError = validateConfirmPassword(passwordData.confirmPassword);
    
    const errors = {
      currentPassword: currentPasswordError,
      newPassword: newPasswordError,
      confirmPassword: confirmPasswordError
    };
    
    setPasswordFormErrors(errors);
    return !currentPasswordError && !newPasswordError && !confirmPasswordError;
  };

  const handlePasswordSubmit = async (e) => {
    e.preventDefault();
    
    if (!validatePasswordForm()) {
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      await changePassword(
        passwordData.currentPassword,
        passwordData.newPassword
      );
      
      showNotification('i mt khu thnh cng!', 'success');
      
      // Reset form
      setPasswordData({
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      });
      setTouchedFields({
        currentPassword: false,
        newPassword: false,
        confirmPassword: false
      });
      setPasswordFormErrors({});
    } catch (error) {
      console.error('Error changing password:', error);
      
      // X l li theo thng ip c tr v
      if (error.response?.data?.message) {
        const errorMessage = error.response.data.message;
        
        // Trng hp mt khu hin ti khng ng
        if (errorMessage.includes('hin ti') || 
            errorMessage.includes('current password') || 
            errorMessage.includes('khng ng')) {
          
          // Ch hin th li, khng ng xut
          setPasswordFormErrors(prev => ({
            ...prev,
            currentPassword: 'Mt khu hin ti khng ng'
          }));
          
          // Thng bo li
          showNotification('Mt khu hin ti khng ng', 'error');
        } else {
          // Cc li khc
          showNotification(errorMessage || 'C li xy ra khi i mt khu', 'error');
          
          // Kim tra nu token cn tn ti (cha b xa bi interceptor)
          if (!localStorage.getItem('token')) {
            // Token  b xa bi interceptor do li xc thc
            navigate('/login');
          }
        }
      } else {
        // Cc li khc khng t API
        showNotification(error.message || 'C li xy ra khi i mt khu', 'error');
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  if (isLoading) {
    return <div className="text-center py-10">ang ti thng tin...</div>;
  }

  if (error) {
    return <div className="text-center py-10 text-red-500">{error}</div>;
  }

  return (
    <div className="container mx-auto p-4">
      {user ? (
        <div className="max-w-4xl mx-auto">
          <div className="bg-white shadow-md rounded-lg p-6 mb-6">
            <h1 className="text-2xl font-bold mb-4">Thng tin ti khon</h1>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="p-4 bg-gray-50 rounded-lg">
                <p className="text-gray-700 mb-2">
                  <span className="font-semibold">Tn ng nhp:</span> {user.username}
                </p>
                <p className="text-gray-700">
                  <span className="font-semibold">Vai tr:</span> {user.role === 'admin' ? 'Qun tr vin' : 'Ngi dng'}
                </p>

                {/* Add Order History Link */}
                <div className="mt-4 pt-3 border-t border-gray-200">
                  <Link 
                    to="/my-orders" 
                    className="inline-flex items-center text-indigo-600 hover:text-indigo-800"
                  >
                    <i className="bi bi-bag mr-2"></i>
                    Xem lch s n hng
                  </Link>
                </div>
              </div>
              
              <div className="p-4 bg-gray-50 rounded-lg">
                <h2 className="text-xl font-semibold mb-3">i mt khu</h2>
                <form onSubmit={handlePasswordSubmit}>
                  <div className="mb-3">
                    <label className="block text-gray-700 text-sm font-medium mb-1" htmlFor="currentPassword">
                      Mt khu hin ti
                    </label>
                    <input
                      id="currentPassword"
                      type="password"
                      name="currentPassword"
                      className={`w-full p-2 border rounded ${passwordFormErrors.currentPassword ? 'border-red-500' : 'border-gray-300'}`}
                      value={passwordData.currentPassword}
                      onChange={handlePasswordChange}
                      onBlur={handleBlur}
                    />
                    {passwordFormErrors.currentPassword && (
                      <p className="text-red-500 text-xs mt-1">{passwordFormErrors.currentPassword}</p>
                    )}
                  </div>
                  
                  <div className="mb-3">
                    <label className="block text-gray-700 text-sm font-medium mb-1" htmlFor="newPassword">
                      Mt khu mi
                    </label>
                    <input
                      id="newPassword"
                      type="password"
                      name="newPassword"
                      className={`w-full p-2 border rounded ${passwordFormErrors.newPassword ? 'border-red-500' : 'border-gray-300'}`}
                      value={passwordData.newPassword}
                      onChange={handlePasswordChange}
                      onBlur={handleBlur}
                    />
                    {passwordFormErrors.newPassword && (
                      <p className="text-red-500 text-xs mt-1">{passwordFormErrors.newPassword}</p>
                    )}
                  </div>
                  
                  <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-medium mb-1" htmlFor="confirmPassword">
                      Xc nhn mt khu mi
                    </label>
                    <input
                      id="confirmPassword"
                      type="password"
                      name="confirmPassword"
                      className={`w-full p-2 border rounded ${passwordFormErrors.confirmPassword ? 'border-red-500' : 'border-gray-300'}`}
                      value={passwordData.confirmPassword}
                      onChange={handlePasswordChange}
                      onBlur={handleBlur}
                    />
                    {passwordFormErrors.confirmPassword && (
                      <p className="text-red-500 text-xs mt-1">{passwordFormErrors.confirmPassword}</p>
                    )}
                  </div>
                  
                  <button
                    type="submit"
                    className={`bg-blue-600 text-white py-2 px-4 rounded hover:bg-blue-700 transition duration-200 ${isSubmitting ? 'opacity-70 cursor-not-allowed' : ''}`}
                    disabled={isSubmitting}
                  >
                    {isSubmitting ? 'ang x l...' : 'i mt khu'}
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div className="text-center py-10">
          <p>Vui lng ng nhp  xem thng tin ti khon</p>
        </div>
      )}
    </div>
  );
};

export default Profile; 