import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate, Link } from 'react-router-dom';
import { getUserById, createUser, updateUser } from '../../services/adminUserService';
import { useNotification } from '../../contexts/NotificationContext';
import Spinner from '../../components/Spinner';
import AdminSidebar from '../../components/AdminSidebar';

const UserForm = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const isEditMode = !!id;
  const { showNotification } = useNotification();
  
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    role: 'user'
  });
  
  const [loading, setLoading] = useState(isEditMode);
  const [submitting, setSubmitting] = useState(false);
  const [errors, setErrors] = useState({});
  
  const fetchUserData = useCallback(async () => {
    if (!isEditMode) return;
    
    try {
      setLoading(true);
      const result = await getUserById(id);
      
      if (result.success) {
        const { username, role } = result.user;
        setFormData({
          username,
          password: '', // Khng hin th mt khu hin ti
          role: role || 'user'
        });
      } else {
        showNotification(result.message || 'Khng th ti thng tin ngi dng', 'error');
        navigate('/admin/users');
      }
    } catch (err) {
      console.error('Error loading user:', err);
      showNotification(' xy ra li khi ti thng tin ngi dng', 'error');
      navigate('/admin/users');
    } finally {
      setLoading(false);
    }
  }, [id, isEditMode, navigate, showNotification]);
  
  useEffect(() => {
    fetchUserData();
  }, [fetchUserData]);
  
  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.username.trim()) {
      newErrors.username = 'Tn ng nhp l bt buc';
    } else if (formData.username.length < 3) {
      newErrors.username = 'Tn ng nhp phi c t nht 3 k t';
    }
    
    if (!isEditMode && !formData.password.trim()) {
      newErrors.password = 'Mt khu l bt buc';
    } else if (formData.password.trim() && formData.password.length < 6) {
      newErrors.password = 'Mt khu phi c t nht 6 k t';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };
  
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
    
    // Clear validation error when field is edited
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: null }));
    }
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }
    
    try {
      setSubmitting(true);
      
      // Prepare data for API call
      const userData = { ...formData };
      if (isEditMode && !userData.password.trim()) {
        delete userData.password; // Don't send empty password on edit
      }
      
      const result = isEditMode
        ? await updateUser(id, userData)
        : await createUser(userData);
      
      if (result.success) {
        showNotification(
          result.message || `Ngi dng  ${isEditMode ? 'cp nht' : 'to'} thnh cng`,
          'success'
        );
        navigate('/admin/users');
      } else {
        showNotification(result.message || 'C li xy ra, vui lng th li', 'error');
      }
    } catch (err) {
      console.error('Error saving user:', err);
      showNotification(' xy ra li khi lu thng tin ngi dng', 'error');
    } finally {
      setSubmitting(false);
    }
  };
  
  if (loading) {
    return <Spinner />;
  }
  
  return (
    <div className="flex h-screen bg-gray-50">
      <AdminSidebar />
      
      <div className="flex-1 p-8 overflow-auto">
        <div className="flex items-center justify-between mb-6">
          <h1 className="text-2xl font-bold text-gray-800">
            {isEditMode ? 'Chnh Sa Ngi Dng' : 'Thm Ngi Dng Mi'}
          </h1>
          <Link 
            to="/admin/users" 
            className="text-indigo-600 hover:text-indigo-900"
          >
            &larr; Quay li danh sch ngi dng
          </Link>
        </div>
        
        <div className="bg-white rounded-lg shadow-md overflow-hidden">
          <form onSubmit={handleSubmit} className="p-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {/* Username Field */}
              <div>
                <label 
                  htmlFor="username" 
                  className="block text-sm font-medium text-gray-700 mb-1"
                >
                  Tn ng nhp <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  id="username"
                  name="username"
                  value={formData.username}
                  onChange={handleChange}
                  className={`w-full px-4 py-2 border rounded-md focus:ring-indigo-500 focus:border-indigo-500 ${
                    errors.username ? 'border-red-500' : 'border-gray-300'
                  }`}
                  disabled={isEditMode && formData.username === 'admin'} // Khng cho php sa ti khon admin
                />
                {errors.username && (
                  <p className="mt-1 text-sm text-red-500">{errors.username}</p>
                )}
              </div>
              
              {/* Password Field */}
              <div>
                <label 
                  htmlFor="password" 
                  className="block text-sm font-medium text-gray-700 mb-1"
                >
                  Mt khu {!isEditMode && <span className="text-red-500">*</span>}
                  {isEditMode && <span className="text-sm text-gray-500 ml-1">( trng nu khng thay i)</span>}
                </label>
                <input
                  type="password"
                  id="password"
                  name="password"
                  value={formData.password}
                  onChange={handleChange}
                  className={`w-full px-4 py-2 border rounded-md focus:ring-indigo-500 focus:border-indigo-500 ${
                    errors.password ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.password && (
                  <p className="mt-1 text-sm text-red-500">{errors.password}</p>
                )}
              </div>
              
              {/* Role Field */}
              <div>
                <label 
                  htmlFor="role" 
                  className="block text-sm font-medium text-gray-700 mb-1"
                >
                  Vai tr <span className="text-red-500">*</span>
                </label>
                <select
                  id="role"
                  name="role"
                  value={formData.role}
                  onChange={handleChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                  disabled={isEditMode && formData.username === 'admin'} // Khng cho php sa vai tr ti khon admin
                >
                  <option value="user">Ngi dng</option>
                  <option value="admin">Qun tr vin</option>
                </select>
              </div>
            </div>
            
            <div className="mt-8 flex justify-end">
              <Link
                to="/admin/users"
                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 mr-4 hover:bg-gray-50"
              >
                Hy
              </Link>
              <button
                type="submit"
                disabled={submitting}
                className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 disabled:bg-indigo-300"
              >
                {submitting ? 'ang lu...' : isEditMode ? 'Cp nht' : 'To mi'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default UserForm; 