import axios from 'axios';
import authHeader from '../utils/authHeader';

const API_URL = 'https://curvotech.onrender.com/api/admin/users';

/**
 * Log  debug request
 */
const logRequest = (method, url, headers) => {
  console.log(`[${method}] ${url}`);
  console.log('Headers:', JSON.stringify(headers, null, 2));
};

/**
 * Ly danh sch ngi dng vi cc ty chn lc v phn trang
 * @param {Object} options - Cc ty chn lc v phn trang
 * @returns {Promise} Danh sch ngi dng v thng tin phn trang
 */
export const getUsers = async ({ search = '', role = '', page = 1, limit = 10, sortBy = 'createdAt', sortOrder = 'desc' }) => {
  try {
    const response = await axios.get(API_URL, {
      headers: authHeader(),
      params: {
        search,
        role,
        page,
        limit,
        sortBy,
        sortOrder
      }
    });
    return response.data;
  } catch (error) {
    console.error('Error fetching users:', error);
    throw error;
  }
};

/**
 * Ly thng tin chi tit ngi dng
 * @param {string} userId - ID ca ngi dng
 * @returns {Promise} Thng tin ngi dng
 */
export const getUserById = async (userId) => {
  try {
    const response = await axios.get(`${API_URL}/${userId}`, {
      headers: authHeader()
    });
    return response.data;
  } catch (error) {
    console.error('Error fetching user by ID:', error);
    throw error;
  }
};

/**
 * To ngi dng mi
 * @param {Object} userData - D liu ngi dng mi
 * @returns {Promise} Thng tin ngi dng mi c to
 */
export const createUser = async (userData) => {
  try {
    const response = await axios.post(API_URL, {
      username: userData.username,
      password: userData.password,
      role: userData.role
    }, {
      headers: authHeader()
    });
    return response.data;
  } catch (error) {
    console.error('Error creating user:', error);
    throw error;
  }
};

/**
 * Cp nht thng tin ngi dng
 * @param {string} userId - ID ca ngi dng cn cp nht
 * @param {Object} userData - D liu ngi dng cn cp nht
 * @returns {Promise} Thng tin ngi dng sau khi cp nht
 */
export const updateUser = async (userId, userData) => {
  try {
    const updateData = {
      username: userData.username,
      role: userData.role
    };
    
    // Ch gi password nu c
    if (userData.password) {
      updateData.password = userData.password;
    }
    
    const response = await axios.put(`${API_URL}/${userId}`, updateData, {
      headers: authHeader()
    });
    return response.data;
  } catch (error) {
    console.error('Error updating user:', error);
    throw error;
  }
};

/**
 * Xa ngi dng
 * @param {string} userId - ID ca ngi dng cn xa
 * @returns {Promise} Kt qu xa
 */
export const deleteUser = async (userId) => {
  try {
    const response = await axios.delete(`${API_URL}/${userId}`, {
      headers: authHeader()
    });
    return response.data;
  } catch (error) {
    console.error('Error deleting user:', error);
    throw error;
  }
}; 