import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import AccessDenied from '../pages/AccessDenied';

const ProtectedRoute = ({ children }) => {
  const { user } = useSelector((state) => state.user);
  const location = useLocation();

  // Kim tra xem ngi dng  ng nhp cha
  if (!user) {
    // Nu cha ng nhp, chuyn hng n trang ng nhp v lu li ng dn hin ti
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  // Kim tra ngi dng c quyn admin khng
  if (user.role !== 'admin') {
    // Nu khng c quyn admin, hin th trang t chi truy cp
    return <AccessDenied />;
  }

  // Nu c quyn admin, cho php truy cp
  return children;
};

export default ProtectedRoute; 