import axios from 'axios';

// Fix API URL to correctly point to the admin products endpoint
const API_URL = process.env.REACT_APP_API_URL || 'https://curvotech.onrender.com/api/admin/products';

// To instance axios vi config chung
const adminAxios = axios.create({
  baseURL: API_URL,
  withCredentials: true
});

// Thm interceptor  t ng thm token vo header
adminAxios.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

/**
 * Ly danh sch sn phm vi phn trang v lc
 * @param {Object} options - Cc ty chn lc v phn trang
 * @param {string} options.search - T kha tm kim
 * @param {string} options.category - Danh mc lc
 * @param {string} options.sort - Sp xp (price_asc, price_desc, name_asc, name_desc, newest)
 * @param {number} options.page - Trang hin ti
 * @param {number} options.limit - S sn phm mi trang
 * @returns {Promise} Danh sch sn phm v thng tin phn trang
 */
export const getProducts = async (options = {}) => {
  try {
    const { search, category, sort, page = 1, limit = 10 } = options;
    
    // Xy dng query string
    const params = new URLSearchParams();
    if (search) params.append('search', search);
    if (category) {
      console.log(`Filtering by category: ${category}`);
      params.append('category', category);
    }
    if (sort) params.append('sort', sort);
    params.append('page', page);
    params.append('limit', limit);
    
    const queryString = params.toString();
    console.log(`Fetching products with query: ${queryString}`);
    
    const response = await adminAxios.get(`?${queryString}`);
    
    // Process products to ensure category format is consistent
    if (response.data && response.data.success && response.data.products) {
      response.data.products = response.data.products.map(product => {
        // Ensure category is in a consistent format
        if (product.category && typeof product.category === 'object') {
          // Make sure we have a categoryName for display
          if (!product.categoryName && product.category.name) {
            product.categoryName = product.category.name;
          }
        }
        return product;
      });
    } else if (!response.data.success) {
      console.error('API returned error:', response.data.message);
    }
    
    return response.data;
  } catch (error) {
    console.error('Li khi ly danh sch sn phm:', error);
    // Provide more detailed error information
    let errorMessage = 'Khng th kt ni n server';
    if (error.response) {
      errorMessage = error.response.data.message || errorMessage;
      console.error('Server response error:', error.response.status, error.response.data);
    } else if (error.request) {
      console.error('No response received:', error.request);
    } else {
      console.error('Error setting up request:', error.message);
    }
    
    return {
      success: false,
      message: errorMessage,
      products: [],
      pagination: { total: 0, page: 1, limit: 10, totalPages: 0 },
      categories: []
    };
  }
};

/**
 * Ly chi tit sn phm theo ID
 * @param {string} productId - ID ca sn phm
 * @returns {Promise} Thng tin chi tit sn phm
 */
export const getProductById = async (productId) => {
  try {
    const response = await adminAxios.get(`/${productId}`);
    return response.data;
  } catch (error) {
    console.error(`Li khi ly sn phm ID ${productId}:`, error);
    return {
      success: false,
      message: error.response?.data?.message || 'Khng th kt ni n server'
    };
  }
};

/**
 * To sn phm mi
 * @param {FormData} productData - Form data cha thng tin sn phm v hnh nh
 * @returns {Promise} Thng tin sn phm mi to
 */
export const createProduct = async (productData) => {
  try {
    const response = await adminAxios.post('/', productData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
    return response.data;
  } catch (error) {
    console.error('Li khi to sn phm:', error);
    return {
      success: false,
      message: error.response?.data?.message || 'Khng th kt ni n server'
    };
  }
};

/**
 * Cp nht sn phm
 * @param {string} productId - ID ca sn phm cn cp nht
 * @param {FormData} productData - Form data cha thng tin sn phm cp nht
 * @returns {Promise} Thng tin sn phm sau khi cp nht
 */
export const updateProduct = async (productId, productData) => {
  try {
    const response = await adminAxios.put(`/${productId}`, productData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
    return response.data;
  } catch (error) {
    console.error(`Li khi cp nht sn phm ID ${productId}:`, error);
    return {
      success: false,
      message: error.response?.data?.message || 'Khng th kt ni n server'
    };
  }
};

/**
 * Xa sn phm
 * @param {string} productId - ID ca sn phm cn xa
 * @returns {Promise} Kt qu xa sn phm
 */
export const deleteProduct = async (productId) => {
  try {
    const response = await adminAxios.delete(`/${productId}`);
    return response.data;
  } catch (error) {
    console.error(`Li khi xa sn phm ID ${productId}:`, error);
    return {
      success: false,
      message: error.response?.data?.message || 'Khng th kt ni n server'
    };
  }
};

/**
 * Ly danh sch cc danh mc sn phm
 * @returns {Promise} Danh sch cc danh mc
 */
export const getCategories = async () => {
  try {
    console.log('Fetching categories from API');
    // Change the endpoint to use the admin categories API instead
    const response = await axios.get('https://curvotech.onrender.com/api/admin/products/categories', {
      withCredentials: true,
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    });
    
    console.log('Categories received:', response.data);
    return response.data;
  } catch (error) {
    console.error('Li khi ly danh sch danh mc:', error);
    let errorMessage = 'Khng th kt ni n server';
    if (error.response) {
      errorMessage = error.response.data.message || errorMessage;
    }
    
    return {
      success: false,
      message: errorMessage,
      categories: [],
      flatCategories: []
    };
  }
}; 