import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { userLogout } from '../store/userSlice';
import { useNotification } from '../contexts/NotificationContext';

const Header = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.user);
  const { showNotification } = useNotification();
  
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoggingOut, setIsLoggingOut] = useState(false);

  // Kim tra xem c thng bo trong state location khng
  useEffect(() => {
    console.log("location state:", location.state);
    if (location.state?.notification) {
      showNotification(
        location.state.notification.message,
        location.state.notification.type || 'success'
      );
      
      // Xa thng bo khi location state  khng hin th li khi refresh
      window.history.replaceState({}, document.title);
    }
  }, [location.state, showNotification]);

  // X l tm kim
  const handleSearchSubmit = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      navigate(`/search?q=${encodeURIComponent(searchQuery.trim())}`);
      setSearchQuery('');
    }
  };

  // X l ng xut
const handleLogout = async () => {
  if (isLoggingOut) return;
  
  try {
    setIsLoggingOut(true);
    await dispatch(userLogout()).unwrap();
    
    // Xa token
    localStorage.removeItem('token');
    
    // Hin th thng bo
    showNotification('ng xut thnh cng!', 'success');

    // i 1 giy ri chuyn hng
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // Chuyn hng v trang login 
    navigate('/login', { replace: true });

  } catch (error) {
    console.error('Li ng xut:', error);
    showNotification('ng xut tht bi!', 'error');
  } finally {
    setIsLoggingOut(false);
  }
};

  return (
    <header className="bg-gray shadow-md relative">
      <div className="container mx-auto p-4 flex items-center justify-between">
        {/* Logo */}
        <div className="flex items-center">
          <img
            src="https://i.pinimg.com/736x/43/5d/09/435d096b52b0be4816d214c05ab0c22e.jpg"
            alt="Logo"
            className="h-20 w-20 rounded-full"
          />
          <span className="ml-2 text-xl font-bold">
            <Link to="/" className="text-gray-800 hover:text-blue-500">
              CurvoTech
            </Link>
          </span>
        </div>

        {/* Navigation Links */}
        <nav className="hidden md:flex space-x-4">
          <strong>
            <Link to="/" className="text-gray-700 hover:text-blue-500 mx-4">
              Trang ch
            </Link>
            <Link to="/products" className="text-gray-700 hover:text-blue-500 mx-4">
              Sn phm
            </Link>
            <Link to="/about" className="text-gray-700 hover:text-blue-500 mx-4">
              Gii thiu
            </Link>
          </strong>
        </nav>

        {/* Search Bar */}
        <div className="flex items-center space-x-2">
          <form onSubmit={handleSearchSubmit} className="relative">
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="border border-gray-300 rounded-full py-2 px-4 pl-10 focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Tm kim..."
            />
            <i className="fas fa-search absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400"></i>
          </form>
        </div>

        {/* Login/Logout */}
        <div className="hidden md:flex items-center space-x-4">
          {user ? (
            <>
              {user.role === 'admin' && (
                <Link
                  to="/admin"
                  className="text-gray-700 hover:text-blue-500 flex items-center"
                >
                  <i className="fas fa-cog mr-2"></i> Qun l Admin
                </Link>
              )}
              <Link
                to="/profile"
                className="text-gray-700 hover:text-blue-500 flex items-center"
              >
                <i className="fas fa-user mr-2"></i> {user.username}
              </Link>
              <Link
                to="/my-orders"
                className="text-gray-700 hover:text-blue-500 flex items-center"
              >
                <i className="fas fa-shopping-bag mr-2"></i> n hng ca ti
              </Link>
              <button
                onClick={handleLogout}
                disabled={isLoggingOut}
                className={`text-gray-700 hover:text-blue-500 flex items-center
                  ${isLoggingOut ? 'opacity-50 cursor-not-allowed' : ''}`}
              >
                {isLoggingOut ? (
                  <>
                    <i className="fas fa-spinner fa-spin mr-2"></i> ang x l...
                  </>
                ) : (
                  <>
                    <i className="fas fa-sign-out-alt mr-2"></i> ng xut
                  </>
                )}
              </button>
            </>
          ) : (
            <>
              <Link
                to="/login"
                className="text-gray-700 hover:text-blue-500 flex items-center"
              >
                <i className="fas fa-sign-in-alt mr-2"></i> ng nhp
              </Link>
              <Link
                to="/register"
                className="text-gray-700 hover:text-blue-500 flex items-center"
              >
                <i className="fas fa-user-plus mr-2"></i> ng k
              </Link>
            </>
          )}
        </div>
      </div>
    </header>
  );
};

export default Header;